/**
 * #.# Editor Styles
 *
 * CSS for just Backend enqueued after style.scss
 * which makes it higher in priority.
 */

 .wp-block-ctx-blocks-button {
     margin-left: 0px !important;
     margin-right: 0px !important;
 }

 .ctx-button  {
    display: inline-block;
    font-weight: 400;
    text-align: center;
    white-space: nowrap;
    vertical-align: middle;
    user-select: none;
    border: 1px solid transparent;
    padding: .375rem .75rem;
    font-size: 1rem;
    line-height: 1.5;
	display: flex;
    align-items: center;
    gap: 7px;
}

.btn-reverse {
	flex-direction: row-reverse;
}



.ctx-button.large {
    padding: 10px 40px !important;
    font-size: 1.3rem !important;
}

.ctx-button.small {
    padding: 0 15px !important;
    line-height: 28px !important;
    font-size: 0.7rem !important;
}


// Ugly Hack to limit URL input width.
.components-panel__body .block-editor-url-input input {
    width: 100% !important;
}

.backdrop {
    position: fixed;
    display: grid;
    place-items: center;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    background-color: rgba(0,0,0,0.5);
    z-index: 1;
}

.ctx-modal {
    background: #fff;
    padding: 50px;
    box-shadow: 0px 3px 20px #000;
    z-index: 2;
    &.full {
        width: 100%;
        height: 100%;
        position: fixed;
        box-shadow: none !important;
    }
}



.ctx-button.large {
    padding: 10px 40px !important;
    font-size: 1.3rem !important;
}

.ctx-button.small {
    padding: 0 15px !important;
    line-height: 28px !important;
    font-size: 0.7rem !important;
}

